document.addEventListener("DOMContentLoaded", (e) => {
  ymaps.ready(init);
  function init() {
    // Создание карты.
    let myMap = new ymaps.Map("map", {
      // Координаты центра карты.
      // Порядок по умолчанию: «широта, долгота».
      // Чтобы не определять координаты центра карты вручную,
      // воспользуйтесь инструментом Определение координат.
      center: [55.76962618521672, 37.63984332012938],
      // Уровень масштабирования. Допустимые значения:
      // от 0 (весь мир) до 19.
      zoom: 12,
    });

    // Создание геообъекта с типом точка (метка).

    let myPlacemark = new ymaps.Placemark(
      [55.76962618521672, 37.63984332012938],
      {},
      {
        iconLayout: "default#image",
        iconImageHref: "images/map-marker.svg",
        iconImageSize: [15, 15],
        iconImageOffset: [-3, -42],
      }
    );

    myPlacemark.events.add(["click"], function (e) {
      document
        .getElementById("map__contacts")
        .classList.add("map__contacts--active");
    });

    // Размещение геообъекта на карте.
    myMap.geoObjects.add(myPlacemark);
  }

 // Бургер

let burgerOpen = document.getElementById("burger__menu")
let headerList = document.getElementById("header__list")
let headerCloseItem =  document.getElementById("header__item-close")
let headerLink = document.getElementById("header__item-link")
let burgerClose = document.getElementById("btn-close__burger")

  burgerOpen.addEventListener('click', () => {
  headerList.classList.add("burger--active--flex");
  headerCloseItem.classList.add("burger--active--block");
  headerLink.classList.add("burger--active--block");
  document.body.classList.add("stop-scroll");
  })

  burgerClose.addEventListener('click', () => {
    headerList.classList.remove("burger--active--flex");
    headerCloseItem.classList.remove("burger--active--block");
    headerLink.classList.remove("burger--active--block");
    document.body.classList.remove("stop-scroll");
    })


  // открытие и закрытие поиска
  document.addEventListener("click", function () {
    document.getElementById("header__search").addEventListener("click", () => {
      document
        .getElementById("search-form")
        .classList.add("search-form--active");
    });

    document
      .getElementById("btn-close__search")
      .addEventListener("click", () => {
        document
          .getElementById("search-form")
          .classList.remove("search-form--active");
      });
  });

  //валидация Подписаться на рассылку
  const validator = new JustValidate("#info__form");

  validator.addField("#info__input", [
    {
      rule: "required",
      errorMessage: "Вы нечего не ввели",
    },
    {
      rule: "email",
      errorMessage: "Недопустимый формат",
    },
  ]);

  const validatorSubmit = new JustValidate("#contacts__form");

  validatorSubmit
    .addField("#contacts__form-name", [
      {
        rule: "required",
        errorMessage: "Вы нечего не ввели",
      },
      {
        rule: "minLength",
        value: 3,
        errorMessage: "Слишком мало символов",
      },
      {
        rule: "maxLength",
        value: 30,
        errorMessage: "Слишком мало символов",
      },
      {
        rule: "customRegexp",
        value: /[А-я]/gi,
        errorMessage: "Недопустимые символы",
      },
    ])
    .addField("#contacts__form-email", [
      {
        rule: "required",
        errorMessage: "Вы нечего не ввели",
      },
      {
        rule: "email",
        errorMessage: "Недопустимый формат",
      },
    ]);

  // добавление контактов по клику, повторный клик уберает конакты
  document.getElementById("contacts__map-btn").addEventListener("click", () => {
    document.getElementById("map__contacts").classList.toggle("map__contacts--active");
  });

  document.getElementById("map__contacts-btn").addEventListener("click", () => {
    document.getElementById("map__contacts").classList.remove("map__contacts--active");
  });
});
